*{
  margin:0;
  padding:0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app{
  font-family: Avenir,Helvetica,Arial,sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}
.btn {
  padding: 10px 20px; /* 按钮内边距 */
  background-color: #8d9093; /* 按钮背景颜色 */
  color: white; /* 按钮文字颜色 */
  border: none; /* 去除边框 */
  border-radius: 5px; /* 圆角边框 */
  cursor: pointer; /* 鼠标悬浮时显示手形指针 */
  text-align: center; /* 文字居中 */
  text-decoration: none; /* 去除文本下划线 */
  display: inline-block; /* 行内块元素 */
  -webkit-transition: background-color 0.3s, color 0.3s;
  transition: background-color 0.3s, color 0.3s; /* 过渡效果 */
}
.btn:hover {
  background-color: #9eabb8; /* 鼠标悬浮时的背景颜色 */
}
.btn:active {
  background-color: #eef0f3; /* 鼠标点击时的背景颜色 */
}


.navs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.navs a{
  text-decoration: none;
    font-weight: 700;
    color: #999;
    -webkit-transition: color .3s;
    transition: color .3s;
    font-size: x-large;
}
